$darkred-color: #a91014;
$red-color: #dd060b;
$blue-color: #006bfe;
$darkblue-color: #002f6e;
$darkgrey-color: #7a7a7a;
$grey-color: #efefef;
$black-color: #272727;
* {
    outline: none;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
}

:focus {
    outline: 0 !important;

    -webkit-appearance: 0;
}

a {
    color: $red-color;
}

a:hover,
a:focus {
    color: $darkred-color;
}

a,
a:focus,
a:not(:focus) {
    transition: .25s linear;
}

.container-fluid {
    max-width: 1920px;
    margin: 0 auto;
}

.container-fluid {
    max-width: 1920px;
}

.container {
    max-width: 1040px;
}

.btn-oval {
    font-size: 22px;
    font-weight: 600;
    line-height: 1;

    width: 260px;
    margin: 0;
    padding: 0;
    padding: 19px;

    transition: .25s linear;
    text-transform: uppercase;

    border-radius: 32px;
}

.btn-opacity {
    text-decoration: none;

    color: #fff;
    border: 2px solid #fff;
    background-color: inherit;
}

.btn-opacity:hover,
.btn-opacity:focus {
    color: $red-color;
    background-color: #fff;
}

.btn-underline {
    font-size: 22px;
    font-weight: 600;
    line-height: 1;

    margin: 0;
    padding: 0;
    padding: 19px;

    text-transform: uppercase;

    color: #fff;
    background-color: inherit;
}

.btn-underline:after {
    display: block;

    width: 100%;
    margin-top: 3px;

    content: '';
    transition: .25s linear;

    border-bottom: 2px solid #fff;
}

.btn-underline:hover {
    color: $darkgrey-color;
}

.btn-underline:hover:after {
    border-bottom: 2px solid $darkgrey-color;
}

.btn-blue {
    text-decoration: none;

    color: #fff;
    background-color: $blue-color;
}

.btn-blue:hover,
.btn-blue:focus {
    color: #fff;
    background-color: $darkblue-color;
}

.btn-red {
    text-decoration: none;

    color: #fff;
    background-color: $red-color;
}

.btn-red:hover,
.btn-red:focus {
    color: #fff;
    background-color: $darkred-color;
}

.border-circle {
    font-size: 1.8em;
    line-height: 1.3;

    display: block;

    width: 1.4em;
    height: 1.4em;
    margin-top: .8em;

    text-align: center;

    border: 2px solid $black-color;
    border-radius: 50%;
}

.red-text {
    color: $red-color;
}

.blue-text {
    color: $blue-color;
}

.box-shadow {
    box-shadow: 0 6px 12px rgba(0, 0, 0, .4);
}

body,
html {
    font-family: 'Montserrat', Helvetica, Arial, sans-serif;

    color: #272727;
}

header {
    & .pull-left {
        margin-left: 15px;
        & .logo {
            display: block;

            margin: 10px 0 7px 15px;
            & img {
                width: 95px;
            }
        }
    }
    & .pull-right {
        margin-right: 15px;
        & .btn {
            font-size: 48px;

            margin: 15px 0 0;
            padding: 0;

            transition: .25s linear;

            border: none;
            background-color: inherit;
            &[aria-expanded='true'] .glyphicon-menu-hamburger,
            &[aria-expanded='false'] .glyphicon-remove {
                display: none;
            }
            &[aria-expanded='false'] {
                color: $black-color;
            }
            &[aria-expanded='true'] {
                color: $darkgrey-color;
            }
        }
        & .menu {
            font-weight: 700;

            list-style: none;

            text-transform: uppercase;
            & li {
                & a {
                    display: block;

                    transition: .25s linear;

                    color: $darkgrey-color;
                    &.active {
                        color: #fff;
                        background: $red-color;
                        &:hover {
                            color: #fff;
                            background: $darkred-color;
                        }
                    }
                    &:focus,
                    &:hover {
                        text-decoration: none;

                        color: $darkgrey-color;
                        background-color: $grey-color;
                    }
                    @media screen and (max-width: 767px) {
                        font-size: 26px;

                        padding: 10px;
                    }
                    @media screen and (min-width: 768px) {
                        font-size: 18px;

                        padding: 32px 15px;
                    }
                }
                @media screen and (min-width: 768px) {
                    float: left;
                }
            }
            @media screen and (max-width: 767px) {
                position: absolute;
                z-index: 2;
                top: 83px;
                right: 0;

                width: 100%;
                padding: 15px;

                text-align: center;

                border-top: 2px solid rgba(0, 0, 0, .075);
                border-bottom: 4px solid rgba(0, 0, 0, .075);
                background-color: #fff;
            }
            @media screen and (min-width: 768px) {
                line-height: 1;

                display: block;
            }
        }
    }
}

section {
    font-size: 16px;

    position: relative;

    overflow: hidden;

    max-width: 100%;
    padding: 50px 0;
    .row {
        position: relative;
    }
    & h2 {
        font-size: 30px;

        margin-top: 0;
        margin-bottom: .6em;
        @media screen and (min-width: 992px) {
            font-size: 36px;
        }
    }
    &.banner {
        padding: 0;

        color: #fff;
        & picture.background {
            position: absolute;
            z-index: -1;
            top: 50%;
            left: 50%;

            display: block;

            width: 1200px;

            transform: translate(-50%, -50%);
            @media screen and (min-width: 992px) {
                width: 1920px;
            }
        }
        & .container {
            padding: 50px 15px;
            & h1 {
                font-size: 32px;
                font-weight: 600;
                line-height: 1.3;

                max-width: 350px;
                margin: 0 0 .4em;
                @media screen and (min-width: 768px) {
                    font-size: 50px;

                    max-width: 520px;
                }
                @media screen and (min-width: 992px) {
                    font-size: 66px;

                    max-width: 640px;
                }
            }
            & p {
                font-size: 21px;
                font-weight: 200;
                line-height: 1.3;

                max-width: 520px;
                margin-bottom: 1em;
                @media screen and (min-width: 768px) {
                    font-size: 24px;

                    max-width: 600px;
                }
                @media screen and (min-width: 992px) {
                    font-size: 28px;

                    max-width: 700px;
                }
            }
            @media screen and (min-width: 768px) {
                padding: 60px 15px;
            }
            @media screen and (min-width: 992px) {
                padding: 70px 15px;
            }
            @media screen and (min-width: 1200px) {
                width: inherit;
                max-width: 1430px;
                padding: 80px;
            }
        }
    }
    &.learn {
        color: $darkgrey-color;
        &.learn-grey {
            max-width: 1920px;
            margin: 0 auto;

            background-color: #eee;
            @media screen and (min-width: 992px) {
                & .col-md-6 {
                    float: right;
                }
            }
        }
        & h2 {
            font-size: 26px;

            margin-top: 1em;

            text-transform: uppercase;

            color: $black-color;
            @media screen and (min-width: 992px) {
                font-size: 30px;
            }
        }
        @media screen and (min-width: 1200px) {
            & .col-img {
                width: 45%;
            }
            & .col-content {
                width: 55%;
                padding-left: 11%;
            }
            &.learn-grey .col-content {
                width: 55%;
                padding-right: 11%;
                padding-left: 15px;
            }
        }
    }
    &.st-today {
        color: #fff;
        & .row {
            margin: 0;
        }
        & picture.background {
            position: absolute;
            z-index: -1;

            display: block;
            overflow: hidden;

            width: 100%;
            height: 100%;

            border-radius: 6px;
        }
        & picture.background img {
            position: absolute;
            z-index: -1;
            top: 50%;
            left: 50%;

            display: block;

            width: 1200px;

            transform: translate(-50%, -50%);
            @media screen and (min-width: 992px) {
                width: 1920px;
            }
        }
        & .container {
            @media screen and (min-width: 1200px) {
                width: inherit;
                max-width: 1430px;
            }
            & .content-block {
                margin-right: auto;
                margin-left: auto;
                padding: 50px;
                & h2 {
                    font-size: 32px;
                    font-weight: 600;
                    line-height: 1.3;

                    margin: 0 0 .4em;
                    @media screen and (min-width: 768px) {
                        font-size: 50px;
                    }
                }
                & p {
                    font-size: 21px;
                    line-height: 1.3;

                    margin-bottom: 1em;
                    @media screen and (min-width: 768px) {
                        font-size: 24px;

                        max-width: 680px;
                    }
                }
                @media screen and (max-width: 480px) {
                    padding: 20px;
                }
                @media screen and (min-width: 992px) {
                    padding: 70px;
                }
                @media screen and (min-width: 1200px) {
                    max-width: 1040px;
                    padding-right: 15px;
                    padding-left: 15px;
                }
            }
        }
    }
    & .button-block {
        & .btn {
            margin-top: 1em;
            &.btn-underline {
                margin-right: 30px;
                margin-left: 30px;
            }
        }
        @media screen and (max-width: 767px) {
            text-align: center;
        }
    }
    @media screen and (min-width: 992px) {
        font-size: 18px;

        padding: 70px 0;
    }
}

footer {
    font-size: 16px;

    padding: 30px 0 50px;

    color: #fff;
    background-color: #000;
    & h3 {
        font-size: 1em;
        line-height: 1.3;

        margin: 20px 0 .8em;

        text-transform: uppercase;
    }
    & a {
        color: #fff;
        &:hover,
        &:focus {
            color: $red-color;
            & svg {
                fill: $red-color;
            }
        }
        & svg {
            transition: .25s linear;

            fill: #fff;
        }
    }
    & .col-sm-6 a {
        line-height: 1.5;

        display: block;
    }
    & .col-xs-12 {
        font-size: 12px;

        margin-top: 40px;
    }
    & ul {
        padding: .5em 0 0;
        & li {
            display: inline-block;

            padding-right: .5em;
            &:first-child {
                margin-left: -.5em;
            }
        }
    }
    @media screen and (min-width: 992px) {
        font-size: 18px;
    }
}

#RegModal .close {
    float: left;
}

#RegModal .modal-header {
    padding: 24px;

    text-transform: uppercase;

    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: #eee;
}

#RegModal .modal-title {
    font-size: 24px;

    color: $black-color;
}

#RegModal button.close {
    font-size: 38px;

    position: absolute;
    left: .75em;

    opacity: .9;
    color: $black-color;
}

#RegModal button.close:hover {
    opacity: .2;
}

#RegModal .modal-content {
    text-align: center;
}

#RegModal .modal-content h5 {
    font-size: 24px;

    color: #95989a;
}

.btn-modal {
    font-size: 28px;
    font-weight: 600;

    width: 260px;
    margin: .5em;
    padding: 1em 0;

    color: #fff;
    border-radius: 6px;
    background-color: $blue-color;
}

.btn-modal:hover,
.btn-modal:focus {
    color: #fff;
    background-color: $darkblue-color;
}

.modal {
    text-align: center;

    background-color: rgba(255, 255, 255, .2);
}

.modal-dialog {
    display: inline-block;

    text-align: left;
    vertical-align: middle;
}

@media screen and (max-width: 767px) {
    .btn-modal {
        font-size: 24px;

        width: 200px;
    }
}

@media screen and (min-width: 768px) {
    .btn-modal {
        font-size: 28px;

        width: 220px;
    }
    .modal:before {
        display: inline-block;

        height: 100%;

        content: ' ';
        vertical-align: middle;
    }
}

@media screen and (min-width: 992px) {
    .btn-modal {
        font-size: 32px;

        width: 260px;
    }
    .modal-dialog {
        width: 920px;
    }
}
